\n        @include ag-theme-rtl(( margin-left: 2px ));\n    }\n\n    .ag-group-title-bar {\n        @include ag-color-property(background-color, subheader-background-color);\n        padding: ag-param(grid-size);\n    }\n    \n    .ag-group-toolbar {\n        padding: ag-param(grid-size);\n    }\n\n    .ag-disabled-group-title-bar, .ag-disabled-group-container {\n        opacity: 0.5\n    }\n\n    .group-item {\n        margin: ag-param(grid-size) / 2 0;\n    }\n\n    .ag-label {\n        white-space: nowrap;\n        @include ag-theme-rtl((margin-right: ag-param(grid-size)));\n    }\n\n    .ag-label-align-top .ag-label {\n        margin-bottom: ag-param(grid-size) / 2;\n    }\n\n    .ag-slider-field,\n    .ag-angle-select-field {\n        @include ag-theme-rtl((margin-right: ag-param(grid-size) * 2));\n    }\n\n    .ag-angle-select-parent-circle {\n        width: 24px;\n        height: 24px;\n        border-radius: 12px;\n        border: solid 1px;\n        @include ag-color-property(border-color, border-color);\n        @include ag-color-property(background-color, background-color);\n    }\n    .ag-angle-select-child-circle {\n        top: 4px;\n        left: 12px;\n        width: 6px;\n        height: 6px;\n        margin-left: -3px;\n        margin-top: -4px;\n        border-radius: 3px;\n        @include ag-color-property(background-color, secondary-foreground-color);\n    }\n\n    .ag-picker-field-wrapper {\n        border: 1px solid;\n        @include ag-color-property(border-color, border-color);\n        border-radius: 5px;\n        &:focus {\n            box-shadow: ag-param(input-focus-box-shadow);\n        }\n    }\n    \n    .ag-picker-field-button {\n        @include ag-color-property(background-color, background-color);\n        @include ag-color-property(color, secondary-foreground-color);\n    }\n\n    .ag-dialog.ag-color-dialog {\n        border-radius: 5px;\n    }\n\n    .ag-color-picker .ag-picker-field-display {\n        height: ag-param(icon-size);\n    }\n\n    .ag-color-panel {\n        padding: ag-param(grid-size);\n    }\n\n    .ag-spectrum-color {\n        background-color: rgb(255, 0, 0);\n        border-radius: 2px;\n    }\n\n    .ag-spectrum-tools {\n        padding: 10px;\n    }\n\n    .ag-spectrum-sat {\n        background-image: linear-gradient(to right, white, rgba(204, 154, 129, 0));\n    }\n\n    .ag-spectrum-val {\n        background-image: linear-gradient(to top, black, rgba(204, 154, 129, 0));\n    }\n\n    .ag-spectrum-dragger {\n        border-radius: 12px;\n        height: 12px;\n        width: 12px;\n        border: 1px solid white;\n        background: black;\n        box-shadow: 0 0 2px 0px rgba(0, 0, 0, 0.24);\n    }\n\n    .ag-spectrum-hue-background {\n        border-radius: 2px;\n    }\n\n    .ag-spectrum-alpha-background {\n        border-radius: 2px;\n    }\n\n    .ag-spectrum-tool {\n        margin-bottom: 10px;\n        height: 11px;\n        border-radius: 2px;\n    }\n\n    .ag-spectrum-slider {\n        margin-top: -12px;\n        width: 13px;\n        height: 13px;\n        border-radius: 13px;\n        background-color: rgb(248, 248, 248);\n        box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.37);\n    }\n\n    .ag-recent-color {\n        margin: 0 3px;\n        &:first-child {\n            margin-left: 0;\n        }\n        &:last-child {\n            margin-right: 0;\n        }\n    }\n\n    ////////////////////////////////////////\n    // Ghost Element (Drag and Drop)\n    ////////////////////////////////////////\n    &.ag-dnd-ghost {\n        @include ag-card();\n        overflow: hidden;\n        text-overflow: ellipsis;\n        z-index: 10;\n        @include ag-border(secondary);\n        @include ag-color-property(color, secondary-foreground-color);\n        height: ag-param(header-height) !important;\n        line-height: ag-param(header-height);\n        margin: 0;\n        padding: 0 ag-param(grid-size) * 2;\n        transform: translateY(ag-param(grid-size) * 2)